*{
    margin: 0;
    box-sizing: border-box;
  }
  :root {
    --prim_font: "Copperplate Gothic", serif;
    --sec_font: "Century Gothic", sans-serif;
    --prim_color: white;
    --sec_color: black;
  }
  .container-signIn {
    width: 100%;
    height: 80vh;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
  }
  .title-signIn {
    font-size: 90px;
    margin-bottom: 60px;
    font-weight: 900;
    text-align: center;
  }
  .input_fields-SignIn input{
    border: 3px solid black;
    align-self: center;
  }
  .input_fields-SignIn {
    width: 19vw;
    margin: auto;
    display: flex;
    flex-direction: column;
    /* border: 3px solid black; */
    font-family: var(--sec_font);  
  }
  
  .box   {
    /* border: 2px solid black; */
    margin: 0;
    padding: 0;
    /* border: 2px solid black; */
    width: auto;
    height: 30px;
    font-size: 20px;
  }
  
  .input_box {
    margin: 0;
    width: 300px;
    height: 30px;
    margin-bottom: 40px;
  }
  .input_box:hover{
    border:3px solid black;
  }
  input{
    background-color: transparent;
  }
  
  .btn{
    /* border: 2px solid black; */
    width: 250px;
    height: 50px;
    background-color: var(--sec_color);
    border-radius: 24px;
    margin: auto;
    display: flex;  
    justify-content: center;
    align-items: center;
    font-family: var(--sec_font);
    font-size: 18px;
    color: white;
  }
  
  .small-icon{
    margin: 0.5rem;
    width: 20px;
    height: 20px;
  }
  